import { defineStore } from 'pinia';
import { usdt_abi, token_abi } from '@/utils/ABI';
import { commonStore } from './common';
// const common_store = commonStore();
import { balanceUtil } from '@/utils/index.js';
import Web3 from 'web3';
const usdt_addr = '0xccF56c1a4f61B64f102749E161DFB306e3d07874';
const token_addr = '0x506D4a37A484fF6b56eFc482DE8666F423b7f7Be';
export const swapStore = defineStore('swap', {
  state: () => {
    return {
      usdtMethods: {},
      usdtEmpower: false,
      tokenMethods: {},
      tokenEmpower: false,
      nftSpender: '0x4261298Fc8e6384476630Ea142DE362283394F65',
      marketSpender: '0x9caaCc10805b8BE8882dd06250aA8e094167e3d8',
      hasEmpower:
        (sessionStorage.getItem('tokenEmpower') &&
          sessionStorage.getItem('tokenEmpower') != 'false') ||
        false,
      address: sessionStorage.getItem('address'),
      marketEmpower:
        (sessionStorage.getItem('marketEmpower') &&
          sessionStorage.getItem('marketEmpower') != 'false') ||
        false,
      usetMarketEmpower:
        (sessionStorage.getItem('usetMarketEmpower') &&
          sessionStorage.getItem('usetMarketEmpower') != 'false') ||
        false,
      balacne: '',
    };
  },
  actions: {
    async getBalance() {
      const common_store = commonStore();
      const balance = await this.tokenMethods
        .balanceOf(common_store.address)
        .call();
      this.balacne = balance > 0 ? balanceUtil(balance + '') : balance;
    },
    setEmpower() {
      this.hasEmpower = true;
      sessionStorage.setItem('tokenEmpower', true);
    },
    setMarketEmpower() {
      this.marketEmpower = true;
      sessionStorage.setItem('marketEmpower', true);
    },
    setUsdtEmpower() {
      this.usetMarketEmpower = true;
      sessionStorage.setItem('setUsdtEmpower', true);
    },
    async getUsdtMethods() {
      const web3 = new Web3(window.ethereum);
      // if (window.ethereum) {
      //   window.ethereum.enable();
      // } else {
      //   return;
      // }
      const rpoContract = new web3.eth.Contract(usdt_abi, usdt_addr);
      this.usdtMethods = rpoContract.methods;
      const common_store = commonStore();
      const marketEmpower = await this.usdtMethods
        .allowance(common_store.address, this.marketSpender)
        .call();

      this.usetMarketEmpower = marketEmpower != 0;
      sessionStorage.getItem('usetMarketEmpower', marketEmpower);
      this.usdtEmpower = true;
    },
    async getTokenMethods() {
      const web3 = new Web3(window.ethereum);

      const rpoContract = new web3.eth.Contract(token_abi, token_addr);
      this.tokenMethods = rpoContract.methods;
      const common_store = commonStore();

      const res = await this.tokenMethods
        .allowance(common_store.address, this.nftSpender)
        .call();
      const marketEmpower = await this.tokenMethods
        .allowance(common_store.address, this.marketSpender)
        .call();
      this.hasEmpower = res > 1000000 * 1e18;
      sessionStorage.setItem('tokenEmpower', this.hasEmpower);
      this.marketEmpower = marketEmpower > 1000000 * 1e18;
      sessionStorage.setItem('marketEmpower', this.marketEmpower);
      this.tokenEmpower = true;
    },
  },
});
